import React, { ReactNode } from "react";
import styled from "styled-components";

interface MainProps {
  children: ReactNode;
}

const InvTemplateBlock = styled.div`
  width: 420px;
  // height: 5000px;

  position: relative; /* 추후 박스 하단에 추가 버튼을 위치시키기 위한 설정 */
  background: white;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.04);

  margin: 0 auto; /* 페이지 중앙에 나타나도록 설정 */

  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
  color: rgb(137, 128, 120);
`;

const InvTemplate = ({ children }: MainProps) => {
  return <InvTemplateBlock>{children}</InvTemplateBlock>;
};

export default InvTemplate;
