import React, { useEffect } from "react";
import styled from "styled-components";
import DirectionsTransitIcon from "@mui/icons-material/DirectionsTransit";
import DirectionsBusIcon from "@mui/icons-material/DirectionsBus";

declare global {
  interface Window {
    kakao: any;
  }
}

const InvLocation = () => {
  useEffect(() => {
    const container = document.getElementById("map"); // 지도가 표시될 HTML element
    const options = {
      center: new window.kakao.maps.LatLng(37.548125151102, 126.92167569614),
      level: 5,
    };
    const map = new window.kakao.maps.Map(container, options);
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const marker = new window.kakao.maps.Marker({
      map: map, // 마커를 표시할 지도 객체
      position: new window.kakao.maps.LatLng(37.548125151102, 126.92167569614), // 마커의 좌표
    });
  }, []);

  const InvLocationBlock = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    .title {
      text-align: center;
      padding-top: 60px;
      padding-bottom: 24px;
      font-size: 32px;
      font-family: "PinyonScript";
    }

    .location__title {
      border: 1px solid #dfd3d3b3;
      width: 350px;
      height: 100px;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;

      .location__title-inner {
        padding: 15px;
      }

      p {
        margin: 10px;
      }
    }

    .location__detail {
      padding-top: 24px;
      text-align: center;
      font-size: 13px;
      color: #cb8c8c;
    }

    .map__box {
      padding-top: 24px;

      #map {
        height: 300px;
        width: 370px;
        position: relative;
        padding: 0;
        overflow: hidden;
      }

      .map__bar {
        height: 40px;
        width: 370px;
        background-color: #dfd3d3b3;
        display: flex;
        justify-content: space-evenly;

        .separator {
          background: #0000001f;
          width: 0.5px;
          border: 0;
          margin-left: 0;
          margin-right: 0;
        }

        .map__bar-kakao,
        .map__bar-google,
        .map__bar-naver {
          flex: 1;

          p {
            font-size: 13px;
            text-align: center;
          }
        }

        .map__bar-kakao:hover,
        .map__bar-google:hover,
        .map__bar-naver:hover {
          cursor: pointer;
        }
      }
    }

    .transport__box {
      padding-top: 36px;
      display: flex;
      flex-direction: column;
      gap: 48px;
      align-items: center;
      justify-content: center;

      .transport__box__item {
        display: flex;
      }

      .transport__icon {
        height: 60px;
        width: 60px;
        background-color: #dfd3d3b3;
        text-align: center;
        display: flex;

        align-items: center;
        justify-content: center;
        border-radius: 50%;
      }

      .transport__info__text {
        padding-left: 10px;
        font-size: 12px;

        .transport__info__text__title {
          padding-bottom: 10px;
          font-size: 14px;
        }

        .transport__info__text_desc {
          line-height: 20px;
        }
      }
    }
  `;
  return (
    <InvLocationBlock>
      <div className="title">Location</div>
      <div>
        <div className="location__title">
          <div className="location__title-inner">
            <p>상수역 1번 출구 근처</p>
            <p>에어비앤비</p>
          </div>
        </div>
      </div>
      <div className="location__detail">
        <p>서울 마포구 독막로15길</p>
        <p>@하우스드밍</p>
      </div>
      <div className="map__box">
        <div id="map"></div>
        <div className="map__bar">
          <div
            className="map__bar-kakao"
            onClick={() =>
              window.open(
                `https://map.kakao.com/link/to/연말파티장소,37.548125151102, 126.92167569614`
              )
            }
          >
            <p>카카오맵</p>
          </div>
          <hr className="separator" />
          <div
            className="map__bar-google"
            onClick={() =>
              window.open(
                "https://www.google.co.kr/maps/dir//%EC%84%9C%EC%9A%B8%ED%8A%B9%EB%B3%84%EC%8B%9C+%EB%A7%88%ED%8F%AC%EA%B5%AC+%EB%8F%85%EB%A7%89%EB%A1%9C15%EA%B8%B8+3-3/data=!4m2!4m1!3e3?hl=ko&entry=ttu"
              )
            }
          >
            <p>구글맵</p>
          </div>
          <hr className="separator" />
          <div
            className="map__bar-naver"
            onClick={() =>
              window.open(
                `https://map.naver.com/p/directions/-/14128887.3788229,4515881.7838529,%EC%84%9C%EC%9A%B8%20%EB%A7%88%ED%8F%AC%EA%B5%AC%20%EB%8F%85%EB%A7%89%EB%A1%9C15%EA%B8%B8%2025,,ADDRESS_POI/-/transit?c=14.00,0,0,0,dh`
              )
            }
          >
            <p>네이버지도</p>
          </div>
        </div>
      </div>
      <div className="transport__box">
        <div className="transport__box__item">
          <div className="transport__icon">
            <DirectionsTransitIcon />
          </div>
          <div className="transport__info__text">
            <div className="transport__info__text__title">지하철 이용시</div>
            <div className="transport__info__text_desc">
              <div>
                ● <b>2호선 합정역</b> 5번 출구 도보 17분
              </div>
              <div>
                ● <b>6호선 상수역</b> 1번 출구 도보 1분
              </div>
            </div>
          </div>
        </div>
        <div className="transport__box__item">
          <div className="transport__icon">
            <DirectionsBusIcon />
          </div>
          <div className="transport__info__text">
            <div className="transport__info__text__title">버스 이용시</div>
            <div className="transport__info__text_desc">
              <div>
                ● <b>지선버스</b> 7011, 7013A, 7013B
              </div>
              <div>
                ● <b>마을버스</b> 마포07
              </div>
            </div>
          </div>
        </div>
      </div>
    </InvLocationBlock>
  );
};

export default InvLocation;
